
export default {
	onLaunch: function() {
		console.log('App Launch')
		let token = uni.getStorageSync('token') || '';
		if(token){
			//更新登陆状态
			uni.getStorage({
				key: 'token',
				success: (res) => {
					this.login(res.data);
				}
			});
		}
		
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
